export default class SearchFormPage {
	constructor(el,selector) {
        this.selector = selector;
        this.root = el?el:document.querySelector(`[${this.selector}]`);
        if (!this.root) return false;
		this._init();
	}

    _init(){
        this._matchHTML();
        this._binds()
        this._initEventListeners()
        this._checkValue()
    }

    _matchHTML() {
        this.input = this.root.querySelector(`[${this.selector}-input]`);
        this.button = this.root.querySelector(`[${this.selector}-button]`);
        this.clear = this.root.querySelector(`[${this.selector}-clear]`);
    } 

    _binds() {
        ['_checkValue','_submitForm', '_clearInput'].forEach(f => this[f] = this[f].bind(this));
    }

    _initEventListeners() {
        this.input.addEventListener('input', this._checkValue);
        this.button.addEventListener('click', this._submitForm);
        this.clear.addEventListener('click', this._clearInput);
    }
    _submitForm() {
        this.root.submit();
    }
    _checkValue() {
        if (this.input.value) {
            this.clear.classList.remove("is-hidden")
        } else {
            this.clear.classList.add("is-hidden")
        }
    }
    _clearInput() {
        this.input.value = '';
        this._checkValue();
    }
    
}